    sortRankList(arr){

let compare = function(obj1,obj2){
    var val1 = obj1.score;
    var val2 = obj2.score;
    if(val1 < val2){
       return 1;
    }else if(val1 > val2){
       return -1;
    }else{
       return 0;
    }
}
let sortArr = arr.sort(compare);
this.rankList = this.playerList;
